import React, { useState, useEffect } from "react";
import {
  Box,
  Typography,
  Paper,
  Avatar,
  IconButton,
  Grid,
  Drawer,
  Divider,
  ToggleButton,
  ToggleButtonGroup,
  Menu,
  MenuItem,
  FormControlLabel,
  Checkbox,
  Pagination,
} from "@mui/material";
import Sensor from "../types/Sensor";
import DeleteIcon from "@mui/icons-material/Delete";
import {
  ArrowForwardIosOutlined,
  FilterList,
  GetAppTwoTone,
  ShareTwoTone,
  TableViewTwoTone,
} from "@mui/icons-material";
import HighlightIcon from "@mui/icons-material/Highlight";
import VisibilityOffIcon from "@mui/icons-material/VisibilityOff";
import Sensors from "@mui/icons-material/Sensors";
import { BomTableDialog } from "./Dialogs/BomTableDialog";

interface SensorPanelProps {
  sensors: Sensor[];
  setSensors: React.Dispatch<React.SetStateAction<Sensor[]>>;
}

const SensorPanel: React.FC<SensorPanelProps> = ({ sensors, setSensors }) => {
  const [drawerOpen, setDrawerOpen] = useState(false);
  const [bomTableDialogOpen, setBomTableDialogOpen] = useState(false);
  const [anchorEl, setAnchorEl] = useState<null | HTMLElement>(null); // 
  const [selectedTypes, setSelectedTypes] = useState<string[]>([]); // 
  const [currentPage, setCurrentPage] = useState(1); // 
  const [itemsPerPage, setItemsPerPage] = useState(5); // 
  const paperHeight = 90; //  Paper  margin  padding

  useEffect(() => {
    const handleResize = () => {
      const windowHeight = window.innerHeight;
      const availableHeight = windowHeight - 200; // 
      const newItemsPerPage = Math.floor(availableHeight / paperHeight); // 
      setItemsPerPage(newItemsPerPage);
    };

    //  itemsPerPage
    handleResize();

    // 
    window.addEventListener("resize", handleResize);

    return () => window.removeEventListener("resize", handleResize);
  }, []);

  //  Drawer /
  const toggleDrawer = () => {
    setDrawerOpen(!drawerOpen);
  };

  //  ToggleButton 
  const handleToggleChange = (
    id: string,
    event: React.MouseEvent<HTMLElement>,
    newOptions: string[] | null
  ) => {
    const updatedConfig = sensors.map((sensor) => {
      if (sensor.id === id) {
        return {
          ...sensor,
          options: newOptions || [], //  options
        };
      } else {
        return sensor;
      }
    });

    setSensors(updatedConfig); // 
    localStorage.setItem("sensorConfig", JSON.stringify(updatedConfig));
  };

  // 
  const handleDeleteClick = (id: string, event: React.MouseEvent) => {
    event.stopPropagation();
    const updatedConfig = sensors.filter((sensor) => sensor.id !== id);
    setSensors(updatedConfig);
    localStorage.setItem("sensorConfig", JSON.stringify(updatedConfig));
  };

  // 
  const handleFilterClick = (event: React.MouseEvent<HTMLButtonElement>) => {
    setAnchorEl(event.currentTarget);
  };

  // 
  const handleFilterClose = () => {
    setAnchorEl(null);
  };

  // spec
  const handleDataTableClick = () => {
    setBomTableDialogOpen(true);
  };

  // 
  const handleTypeChange = (type: string) => {
    if (selectedTypes.includes(type)) {
      setSelectedTypes(selectedTypes.filter((t) => t !== type)); // 
    } else {
      setSelectedTypes([...selectedTypes, type]); // 
    }
  };

  // 
  const filteredSensors = selectedTypes.length
    ? sensors.filter((sensor) => selectedTypes.includes(sensor.sensorInfo.type))
    : sensors; // 

  // 
  const indexOfLastItem = currentPage * itemsPerPage;
  const indexOfFirstItem = indexOfLastItem - itemsPerPage;
  const currentSensors = filteredSensors.slice(
    indexOfFirstItem,
    indexOfLastItem
  );

  // 
  const handlePageChange = (
    event: React.ChangeEvent<unknown>,
    page: number
  ) => {
    setCurrentPage(page);
  };

  return (
    <>
      {/*  IconButton  z-index  */}
      <IconButton
        onClick={toggleDrawer}
        sx={{
          position: "fixed",
          color: "#0c7a92",
          top: 50,
          right: 0,
          fontSize: "40px",
          zIndex: 1400, // 
        }}
      >
        {!drawerOpen && <Sensors sx={{ fontSize: "40px" }} />}
      </IconButton>

      <Drawer
        anchor="right"
        open={drawerOpen}
        onClose={toggleDrawer}
        variant="persistent"
        PaperProps={{ sx: { overflow: "visible" } }}
      >
        <Box
          sx={{
            width: "20vw",
            height: "100vh",
            display: "flex",
            flexDirection: "column",
          }}
        >
          <Box
            sx={{
              padding: "10px",
              backgroundColor: "#0c7a92",
              color: "white",
              display: "flex",
              justifyContent: "space-between",
              alignItems: "center",
            }}
          >
            <Typography variant="h6">Sensor Set</Typography>

            <IconButton
              onClick={toggleDrawer}
              sx={{
                position: "fixed",
                color: "#0c7a92",
                top: 0,
                right: 0,
                fontSize: "40px",
                zIndex: 1400, // 
              }}
            >
              <ArrowForwardIosOutlined
                sx={{ fontSize: "40px", color: "white" }}
              />
            </IconButton>
          </Box>

          <Menu
            id="filter-menu"
            anchorEl={anchorEl}
            open={Boolean(anchorEl)}
            onClose={handleFilterClose}
          >
            <MenuItem>
              <FormControlLabel
                control={
                  <Checkbox
                    checked={selectedTypes.includes("uss")}
                    onChange={() => handleTypeChange("uss")}
                  />
                }
                label="USS"
              />
            </MenuItem>
            <MenuItem>
              <FormControlLabel
                control={
                  <Checkbox
                    checked={selectedTypes.includes("lidar")}
                    onChange={() => handleTypeChange("lidar")}
                  />
                }
                label="Lidar"
              />
            </MenuItem>
            <MenuItem>
              <FormControlLabel
                control={
                  <Checkbox
                    checked={selectedTypes.includes("camera")}
                    onChange={() => handleTypeChange("camera")}
                  />
                }
                label="Camera"
              />
            </MenuItem>
            <MenuItem>
              <FormControlLabel
                control={
                  <Checkbox
                    checked={selectedTypes.includes("radar")}
                    onChange={() => handleTypeChange("radar")}
                  />
                }
                label="Radar"
              />
            </MenuItem>
          </Menu>

          <Divider />

          <Box
            sx={{
              flexGrow: 1,
              paddingRight: "10px",
              paddingLeft: "10px",
              position: "relative", // 
            }}
          >
            <Box
              sx={{
                display: "flex",
                justifyContent: "flex-end", // 
                alignItems: "center", // 
              }}
            >
              <IconButton
                aria-controls="Export-menu"
                aria-haspopup="true"
                onClick={() => {}}
              >
                <GetAppTwoTone />
              </IconButton>
              <IconButton
                aria-controls="bom-menu"
                aria-haspopup="true"
                onClick={handleDataTableClick}
              >
                <TableViewTwoTone />
              </IconButton>
              <IconButton
                aria-controls="share-menu"
                aria-haspopup="true"
                onClick={() => {}}
              >
                <ShareTwoTone />
              </IconButton>
              <IconButton
                aria-controls="filter-menu"
                aria-haspopup="true"
                onClick={handleFilterClick}
              >
                <FilterList />
              </IconButton>
            </Box>

            {currentSensors.map((sensor) => (
              <Box
                key={sensor.id} //  uuid  key
                sx={{
                  position: "relative",
                  display: "block", // 
                  overflow: "visible", // 
                  transition: "margin-left 0.3s ease", //  margin-left  translateX
                  marginLeft: "0px", // 
                  "&:hover": {
                    marginLeft: "-20px", // 
                  },
                }}
              >
                <Paper
                  elevation={3}
                  sx={{
                    padding: "5px",
                    height: "80px",
                    marginBottom: "5px",
                    backgroundColor: "#ffffff",
                    cursor: "pointer",
                    display: "flex",
                    alignItems: "center",
                    borderRadius: "40px 0px 0px 40px", // 
                  }}
                >
                  <Box
                    sx={{
                      display: "flex",
                      alignItems: "center",
                      justifyContent: "center",
                      width: 80,
                      height: "100%",
                      borderRadius: "50%", // 
                    }}
                  >
                    <Avatar
                      src={sensor.sensorInfo.image || undefined}
                      alt={sensor.sensorInfo.name}
                      sx={{
                        width: 40,
                        height: 40,
                      }}
                    >
                      {!sensor.sensorInfo.image && sensor.sensorInfo.name![0]}
                    </Avatar>
                  </Box>

                  <Grid container alignItems="center" spacing={2}>
                    <Grid item xs sx={{ m: "4px" }}>
                      <Typography variant="h1" sx={{ fontSize: "18px" }}>
                        {sensor.sensorInfo.name}
                      </Typography>
                      {/* <PinDrop /> */}
                      <Typography variant="body2" sx={{ fontSize: "12px" }}>
                        {sensor.mountPosition!.name}
                      </Typography>
                    </Grid>
                  </Grid>

                  <IconButton
                    onClick={(event) => handleDeleteClick(sensor.id, event)} //  uuid
                    size="small"
                    sx={{
                      position: "absolute",
                      top: 4,
                      right: 4,
                      backgroundColor: "#097c74",
                      color: "white",
                      boxShadow: "0px 4px 8px rgba(0, 0, 0, 0.3)",
                      width: 30,
                      height: 30,
                      "&:hover": {
                        backgroundColor: "#ff1744",
                        color: "white",
                      },
                    }}
                  >
                    <DeleteIcon sx={{ fontSize: "16px" }} />
                  </IconButton>

                  {/*  ToggleButtonGroup  */}
                  <Box
                    sx={{
                      position: "absolute",
                      right: 10,
                      bottom: 10,
                      display: "flex",
                      justifyContent: "right",
                    }}
                  >
                    <ToggleButtonGroup
                      value={sensor.options || []}
                      onChange={
                        (event, newOptions) =>
                          handleToggleChange(sensor.id, event, newOptions) //  uuid
                      }
                      aria-label="sensor options"
                      size="small"
                      exclusive={false} // 
                    >
                      <ToggleButton
                        value="highlight"
                        aria-label="highlight"
                        sx={{
                          width: 22,
                          height: 22,
                          "&.Mui-selected": {
                            backgroundColor: "#efefef", // 
                            color: "black", // 
                          },
                        }}
                      >
                        <HighlightIcon sx={{ fontSize: "16px" }} />
                      </ToggleButton>

                      <ToggleButton
                        value="hide"
                        aria-label="hide"
                        sx={{
                          width: 22,
                          height: 22,
                          "&.Mui-selected": {
                            backgroundColor: "#efefef", // 
                            color: "black", // 
                          },
                        }}
                      >
                        <VisibilityOffIcon sx={{ fontSize: "16px" }} />
                      </ToggleButton>
                    </ToggleButtonGroup>
                  </Box>
                </Paper>
              </Box>
            ))}
          </Box>

          {/*  */}
          <Box sx={{ p: 2, display: "flex", justifyContent: "center" }}>
            <Pagination
              count={Math.ceil(filteredSensors.length / itemsPerPage)}
              page={currentPage}
              onChange={handlePageChange}
              sx={{
                "& .MuiPaginationItem-root": {
                  margin: "0 1px", // 
                  padding: "2px 3px", // 
                },
                "& .Mui-selected": {
                  backgroundColor: "#0c7a92", // 
                  color: "white", // 
                },
              }}
            />
          </Box>
        </Box>
      </Drawer>
      <BomTableDialog
        open={bomTableDialogOpen}
        setBomTableDialogOpen={setBomTableDialogOpen}
        sensors={sensors}
      />
    </>
  );
};

export default SensorPanel;
