import React, { useState } from "react";
import {
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  TextField,
  Button,
  MenuItem,
} from "@mui/material";
import { SensorItem } from "../../types/Common";
import { v4 as uuidv4 } from "uuid";
interface CreateSensorDialogProps {
  open: boolean;
  onClose: () => void;
  onCreate: (sensorItem: SensorItem) => void;
  existingTypes: string[]; // 
  defaultType: string; // 
}

const CreateSensorDialog: React.FC<CreateSensorDialogProps> = ({
  open,
  onClose,
  onCreate,
  existingTypes,
  defaultType,
}) => {
  const [newSensor, setNewSensor] = useState<SensorItem>({
    id: uuidv4(),
    name: "",
    type: defaultType, // 
    desc: "",
    image: "",
    brand: "unknow",
    spec: {
      range: 0,
      fov: 0,
    },
    attr: {
      promotion: false,
      new: false,
    },
  });

  const [isOtherType, setIsOtherType] = useState(false);

  const handleInputChange = (
    event: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>
  ) => {
    const { name, value } = event.target;
    setNewSensor((prev) => ({
      ...prev,
      name: value,
    }));
  };

  const handleTypeChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    const value = event.target.value;
    if (value === "Other") {
      setIsOtherType(true);
      setNewSensor((prev) => ({
        ...prev,
        type: "", // 
      }));
    } else {
      setIsOtherType(false);
      setNewSensor((prev) => ({
        ...prev,
        type: value,
      }));
    }
  };

  const handleOtherTypeInput = (event: React.ChangeEvent<HTMLInputElement>) => {
    const value = event.target.value;
    setNewSensor((prev) => ({
      ...prev,
      type: value, // 
    }));
  };

  const handleCreate = () => {
    onCreate(newSensor);
    onClose();
  };

  return (
    <Dialog open={open} onClose={onClose}>
      <DialogTitle>Create New Sensor</DialogTitle>
      <DialogContent>
        <TextField
          margin="dense"
          name="name"
          label="Sensor Name"
          fullWidth
          variant="standard"
          onChange={handleInputChange}
        />
        <TextField
          margin="dense"
          name="desc"
          label="Description"
          fullWidth
          variant="standard"
          onChange={handleInputChange}
        />
        <TextField
          margin="dense"
          name="type"
          label="Type"
          select
          fullWidth
          variant="standard"
          value={defaultType} // 
          onChange={handleTypeChange}
        >
          {existingTypes.map((type) => (
            <MenuItem key={type} value={type}>
              {type}
            </MenuItem>
          ))}
          <MenuItem value="Other">Other</MenuItem>
        </TextField>
        {isOtherType && (
          <TextField
            margin="dense"
            name="type"
            label="Specify Type"
            fullWidth
            variant="standard"
            onChange={handleOtherTypeInput} // 
          />
        )}
        <TextField
          margin="dense"
          name="range"
          label="Range (m)"
          type="number"
          fullWidth
          variant="standard"
          onChange={(e) =>
            setNewSensor((prev) => ({
              ...prev,
              spec: { ...prev.spec, range: Number(e.target.value) },
            }))
          }
        />
        <TextField
          margin="dense"
          name="fov"
          label="Field of View ()"
          type="number"
          fullWidth
          variant="standard"
          onChange={(e) =>
            setNewSensor((prev) => ({
              ...prev,
              spec: { ...prev.spec, fov: Number(e.target.value) },
            }))
          }
        />
      </DialogContent>
      <DialogActions>
        <Button
          onClick={handleCreate}
          variant="contained"
          sx={{ minWidth: 100, color: "white", backgroundColor: "#0c7a92" }}
        >
          Create
        </Button>
        <Button onClick={onClose}>Cancel</Button>
      </DialogActions>
    </Dialog>
  );
};

export default CreateSensorDialog;
